html {
  font-size: 16px;
  scroll-behavior: smooth;
  scroll-padding-top: 4.5rem;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  margin-inline: auto;
  max-width: 80rem;
  font-family: "Exo 2";
}

header {
  min-height: 4.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #fff;
  position: sticky;
  top: 0;
  z-index: 1;
}
header > nav > ul {
  display: flex;
  gap: 2rem;
  list-style: none;
  margin: 0;
  padding: 0;
}
header > nav > ul > li > a {
  text-decoration: none;
  color: black;
  text-transform: capitalize;
}
header > nav > ul > li > a:hover {
  color: #f35333;
}

footer {
  background-color: #333333;
  min-height: 16rem;
  color: white;
}

header > .logo {
  padding-inline: 1rem;
}

header > .logo > img {
  width: 12.5rem;
  height: auto;
}

header > .actions {
  padding-inline: 1rem;
}

header > .actions > button {
  font-size: 1rem;
  appearance: none;
  border: none;
  background-color: #f35333;
  color: white;
  padding-inline: 2rem;
  padding-block: 0.75rem;
  border-radius: 0.5rem;
  text-transform: uppercase;
  font-weight: bold;
}

#home {
  display: flex;
  background-color: initial;
  padding: 1rem 1rem 1rem 4rem;
}

section {
  min-height: 40rem;
}

[alt=hero] {
  height: 30rem;
}

.home-content {
  flex-grow: 1;
  position: relative;
}

.home-content > h1 {
  font-family: serif;
  font-size: 4rem;
  font-weight: normal;
}

.home-content > ul {
  position: absolute;
  left: -4.5rem;
  list-style: none;
  top: 1rem;
}

.home-content > ul img {
  width: 1rem;
  margin-block: 0.5rem;
}

.home-content > div + button {
  background-color: orangered;
  color: white;
  appearance: none;
  border: none;
  border-radius: 0.25rem;
  padding: 0.5rem 1rem;
}

#about {
  padding-top: 2rem;
  background-color: #f6f6f6;
}

#about > h1 {
  text-align: center;
}

#about > p {
  text-align: center;
}

#about > div {
  display: flex;
  gap: 2rem;
  padding-inline: 4rem;
}

#about div.card {
  text-align: center;
  background-color: white;
  border-radius: 1rem;
  box-shadow: 0.25rem 0.25rem 0.5rem 0.5rem #eee;
  padding: 2rem;
}

#about div > img {
  width: 3rem;
}

#about div.card > h2 {
  color: orange;
  font-size: 1rem;
}

#about div.card > p {
  color: #666;
  font-size: 0.75rem;
}

.center-text {
  text-align: center;
}

.slider {
  display: flex;
  gap: 1rem;
  margin-top: 8rem;
}

.slider > div {
  flex-grow: 1;
  border-radius: 1rem;
  background-color: red;
  overflow: hidden;
}

.slider > div > img {
  width: 100%;
  vertical-align: bottom;
}

#contact {
  padding-top: 2rem;
  background-color: #f5f5f5;
}

.line {
  width: 8rem;
  height: 2px;
  background-color: orangered;
  margin: auto;
}

[type=email]:invalid {
  border: 1px solid red;
}

label {
  margin: 0.5rem 0 0.25rem;
  display: block;
}

input[type=text],
input[type=email],
textarea {
  padding: 0.25rem 0.5rem;
  border: 1px solid silver;
  border-radius: 0.25rem;
  line-height: 1.25rem;
  width: 100%;
}

form input[type=submit],
form input[type=reset] {
  font-size: 1rem;
  appearance: none;
  border: none;
  background-color: #f35333;
  color: white;
  padding-inline: 2rem;
  padding-block: 0.75rem;
  border-radius: 0.5rem;
  text-transform: uppercase;
  font-weight: bold;
}

form input[type=reset] {
  background-color: gray;
}